import React from "react";
import Img from "gatsby-image";
import { useStaticQuery, graphql} from 'gatsby';
import Button from '../../button/button.component';

export default function ImageCard(props) {

    const data = useStaticQuery(graphql`

    query galleryHomePageMH {
      # Maarten_en_Heiline
      image: file(relativePath: { eq: "gallery/Maarten_en_Heiline/Ceremony-7.jpg" }) {
        childImageSharp {
          # Specify the image processing specifications right in the query.
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
      `
    )

    return (      
        <div class="column-gallery">
            <div>
            <div class="gallery-card-image-wrapper">
                <Img fluid={data.image.childImageSharp.fluid} alt={props.galleryName}/>
                <div class="description">{props.galleryName}</div>
            </div>
            <div>
            <Button CTA="bekijk hele album" class="btn btn-light" ctaLink="/portfolio/maarten-en-heiline"></Button>
            </div>
            </div>
        </div>
  
  )
}